// RESET
@mixin link-reset {
  color: $color-default-black;
  text-decoration: none;

  cursor: pointer;
}

@mixin button-reset {
  background-color: transparent;
  border: none;
  outline: 0;
  cursor: pointer;
}

@mixin ul-reset {
  margin: 0;
  padding: 0;

  list-style-type: none;
}

@mixin li-reset-last-el {
  &:last-child {
    margin: 0;
  }
}

@mixin fieldset-reset {
  margin: 0;
  padding: 0;

  border: 0;

  appearance: none;
}

// TITLE
@mixin title($f-size: 54px, $l-spacing: 1.62px) {
  margin: 0;

  font-weight: 600;
  font-size: 54px;
  line-height: normal;
  font-family: $ff-alumni;
  color: $color-primary;
  letter-spacing: $l-spacing;
}

//СОСТОЯНИЕ
@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// VIEWPORTS
// Desktop first

@mixin vp-1919 {
  @media (max-width: ($vp-1920 - 1px)) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: ($vp-1440 - 1px)) {
    @content;
  }
}

@mixin vp-1279 {
